@import 'modern-normalize/modern-normalize.css';

:root {
  --color-accent: rgb(42, 48, 11);
  --color-bg: #f3f0e9;
  --color-dark: #3c3c3c;
  --color-light: rgb(18, 193, 216);
}

body {
  font-family: "Red Hat Text", sans-serif;
  background-color: floralwhite;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

ul,
ol {
  padding: 0;
  margin: 0;

  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}
